<script lang="ts">
	import { onMount, onDestroy } from 'svelte';
	import { streamProxyService } from '$lib/services/streamProxy';
	import { StreamingError, ErrorCategory, ErrorSeverity } from '$lib/types/errors';
	import { logger } from '$lib/utils/logger';
	import type { PlayerState, BufferInfo } from '$lib/types/api';

	interface Props {
		src: string;
		poster?: string;
		title?: string;
		onError?: (error: StreamingError) => void;
		onLoad?: () => void;
		onBufferUpdate?: (bufferInfo: BufferInfo) => void;
	}

	let { src, poster, title, onError, onLoad, onBufferUpdate }: Props = $props();
	let videoElement: HTMLVideoElement;
	let bufferProgress = $state(0);
	let loadProgress = $state(0);
	let isBuffering = $state(true);
	let bufferHealth = $state(0);
	let playerState = $state<PlayerState>({
		playing: false,
		buffering: true,
		muted: false,
		volume: 1,
		currentTime: 0,
		duration: 0,
		playbackRate: 1,
		buffered: null,
		seekable: null,
		error: null,
		networkState: 0,
		readyState: 0
	});

	// Process the stream URL through our proxy
	const proxiedSrc = $derived(streamProxyService.processStreamUrl(src, true));

	let bufferMonitorInterval: ReturnType<typeof setInterval> | null = null;
	let loadTimeout: ReturnType<typeof setTimeout> | null = null;
	let retryCount = 0;
	const maxRetries = 3;
	const loadTimeoutMs = 30000; // 30 seconds

	onMount(async () => {
		try {
			if (!videoElement) {
				throw StreamingError.createPlayerError('Video element not available');
			}

			logger.player('info', `Initializing buffering player for: ${title}`, {
				src: proxiedSrc,
				poster,
				userAgent: navigator.userAgent
			});

			await initializePlayer();
		} catch (error) {
			handleStreamingError(error);
		}
	});

	onDestroy(() => {
		cleanup();
	});

	async function initializePlayer(): Promise<void> {
		try {
			// Set load timeout
			loadTimeout = setTimeout(() => {
				handleStreamingError(
					StreamingError.createStreamError(
						`Stream load timeout after ${loadTimeoutMs}ms`,
						undefined,
						{ metadata: { src: proxiedSrc, timeout: loadTimeoutMs } }
					)
				);
			}, loadTimeoutMs);

			// Initialize video element
			videoElement.src = proxiedSrc;
			videoElement.preload = 'auto';
			videoElement.crossOrigin = 'anonymous';

			// Set up event listeners
			setupEventListeners();

			// Set up buffering monitoring
			setupBufferMonitoring();

			// Start loading
			videoElement.load();
		} catch (error) {
			throw StreamingError.createPlayerError(
				'Failed to initialize player',
				error instanceof Error ? error : new Error(String(error))
			);
		}
	}

	function setupEventListeners(): void {
		const events = [
			{ name: 'loadstart', handler: handleLoadStart },
			{ name: 'loadedmetadata', handler: handleLoadedMetadata },
			{ name: 'loadeddata', handler: handleLoadedData },
			{ name: 'canplay', handler: handleCanPlay },
			{ name: 'canplaythrough', handler: handleCanPlayThrough },
			{ name: 'progress', handler: handleProgress },
			{ name: 'waiting', handler: handleWaiting },
			{ name: 'playing', handler: handlePlaying },
			{ name: 'pause', handler: handlePause },
			{ name: 'timeupdate', handler: handleTimeUpdate },
			{ name: 'durationchange', handler: handleDurationChange },
			{ name: 'volumechange', handler: handleVolumeChange },
			{ name: 'ratechange', handler: handleRateChange },
			{ name: 'error', handler: handleError },
			{ name: 'abort', handler: handleAbort },
			{ name: 'stalled', handler: handleStalled },
			{ name: 'suspend', handler: handleSuspend }
		];

		events.forEach(({ name, handler }) => {
			videoElement.addEventListener(name, handler);
		});
	}

	function cleanup(): void {
		if (loadTimeout) {
			clearTimeout(loadTimeout);
			loadTimeout = null;
		}

		if (bufferMonitorInterval) {
			clearInterval(bufferMonitorInterval);
			bufferMonitorInterval = null;
		}

		// Clean up event listeners
		if (videoElement) {
			const events = [
				'loadstart',
				'loadedmetadata',
				'loadeddata',
				'canplay',
				'canplaythrough',
				'progress',
				'waiting',
				'playing',
				'pause',
				'timeupdate',
				'durationchange',
				'volumechange',
				'ratechange',
				'error',
				'abort',
				'stalled',
				'suspend'
			];

			events.forEach((eventName) => {
				videoElement.removeEventListener(eventName, handleLoadStart);
			});
		}

		logger.player('debug', 'Player cleanup completed');
	}

	function setupBufferMonitoring(): void {
		bufferMonitorInterval = setInterval(() => {
			try {
				if (videoElement && videoElement.buffered.length > 0) {
					updatePlayerState();
					updateBufferInfo();
				}
			} catch (error) {
				logger.player('warn', 'Buffer monitoring error', { error });
			}
		}, 1000);
	}

	function updatePlayerState(): void {
		if (!videoElement) return;

		playerState.playing = !videoElement.paused;
		playerState.muted = videoElement.muted;
		playerState.volume = videoElement.volume;
		playerState.currentTime = videoElement.currentTime;
		playerState.duration = videoElement.duration || 0;
		playerState.playbackRate = videoElement.playbackRate;
		playerState.buffered = videoElement.buffered;
		playerState.seekable = videoElement.seekable;
		playerState.error = videoElement.error;
		playerState.networkState = videoElement.networkState;
		playerState.readyState = videoElement.readyState;
		playerState.buffering = isBuffering;
	}

	function updateBufferInfo(): void {
		if (!videoElement || videoElement.buffered.length === 0) return;

		const currentTime = videoElement.currentTime;
		const duration = videoElement.duration || 0;

		// Calculate buffer progress
		let bufferedEnd = 0;
		let bufferedStart = 0;

		for (let i = 0; i < videoElement.buffered.length; i++) {
			const start = videoElement.buffered.start(i);
			const end = videoElement.buffered.end(i);

			if (currentTime >= start && currentTime <= end) {
				bufferedEnd = end;
				bufferedStart = start;
				break;
			}
		}

		bufferProgress = duration > 0 ? (bufferedEnd / duration) * 100 : 0;

		// Calculate buffer health (how much is buffered ahead)
		const bufferAhead = Math.max(0, bufferedEnd - currentTime);
		const bufferBehind = Math.max(0, currentTime - bufferedStart);
		bufferHealth = Math.min(bufferAhead / 30, 1) * 100; // 30 seconds = 100% health

		// Update buffering state based on ready state and buffer health
		isBuffering = videoElement.readyState < 3 || (bufferHealth < 10 && !videoElement.paused);

		// Calculate total buffered time
		let totalBuffered = 0;
		for (let i = 0; i < videoElement.buffered.length; i++) {
			totalBuffered += videoElement.buffered.end(i) - videoElement.buffered.start(i);
		}

		const bufferInfo: BufferInfo = {
			buffered: bufferProgress,
			health: bufferHealth,
			ahead: bufferAhead,
			behind: bufferBehind,
			total: totalBuffered
		};

		onBufferUpdate?.(bufferInfo);
	}

	function handleStreamingError(error: unknown): void {
		let streamingError: StreamingError;

		if (error instanceof StreamingError) {
			streamingError = error;
		} else if (error instanceof Error) {
			streamingError = StreamingError.createPlayerError(error.message, error);
		} else {
			streamingError = StreamingError.createPlayerError(String(error));
		}

		logger.player('error', 'Streaming error occurred', {
			errorId: streamingError.errorId,
			category: streamingError.category,
			severity: streamingError.severity,
			message: streamingError.message,
			userMessage: streamingError.userMessage,
			retryable: streamingError.retryable
		});

		// Attempt retry if error is retryable
		if (streamingError.retryable && retryCount < maxRetries) {
			retryCount++;
			logger.player('info', `Attempting retry ${retryCount}/${maxRetries}`);

			setTimeout(() => {
				initializePlayer().catch((retryError) => {
					handleStreamingError(retryError);
				});
			}, 2000 * retryCount); // Exponential backoff
			return;
		}

		// Report error to parent component
		onError?.(streamingError);
	}

	// Event handlers
	function handleLoadStart(): void {
		logger.player('debug', 'Load started');
		loadProgress = 0;
		isBuffering = true;
		updatePlayerState();
	}

	function handleLoadedMetadata(): void {
		logger.player('debug', 'Metadata loaded', {
			duration: videoElement.duration,
			videoWidth: videoElement.videoWidth,
			videoHeight: videoElement.videoHeight
		});
		loadProgress = 25;
		updatePlayerState();
	}

	function handleLoadedData(): void {
		logger.player('debug', 'Data loaded');
		loadProgress = 50;
		updatePlayerState();
	}

	function handleCanPlay(): void {
		logger.player('debug', 'Can play');
		loadProgress = 75;
		isBuffering = false;

		// Clear load timeout on successful load
		if (loadTimeout) {
			clearTimeout(loadTimeout);
			loadTimeout = null;
		}

		onLoad?.();
		updatePlayerState();
	}

	function handleCanPlayThrough(): void {
		logger.player('debug', 'Can play through');
		loadProgress = 100;
		isBuffering = false;
		updatePlayerState();
	}

	function handleProgress(): void {
		// This handles download progress, not playback progress
		if (videoElement && videoElement.buffered.length > 0) {
			const buffered = videoElement.buffered.end(videoElement.buffered.length - 1);
			const duration = videoElement.duration || 0;
			if (duration > 0) {
				loadProgress = Math.min((buffered / duration) * 100, 100);
			}
		}
		updatePlayerState();
	}

	function handleWaiting(): void {
		logger.player('debug', 'Buffering...');
		isBuffering = true;
		updatePlayerState();
	}

	function handlePlaying(): void {
		logger.player('debug', 'Playing');
		isBuffering = false;
		updatePlayerState();
	}

	function handlePause(): void {
		logger.player('debug', 'Paused');
		isBuffering = false;
		updatePlayerState();
	}

	function handleTimeUpdate(): void {
		// Throttle state updates to avoid excessive rendering
		if (Date.now() % 1000 < 100) {
			// Update roughly every second
			updatePlayerState();
		}
	}

	function handleDurationChange(): void {
		logger.player('debug', 'Duration changed', { duration: videoElement.duration });
		updatePlayerState();
	}

	function handleVolumeChange(): void {
		logger.player('debug', 'Volume changed', {
			volume: videoElement.volume,
			muted: videoElement.muted
		});
		updatePlayerState();
	}

	function handleRateChange(): void {
		logger.player('debug', 'Playback rate changed', { rate: videoElement.playbackRate });
		updatePlayerState();
	}

	function handleError(event: Event): void {
		const mediaError = videoElement.error;
		let errorMessage = 'Unknown video error';
		let errorCategory = ErrorCategory.PLAYER;

		if (mediaError) {
			switch (mediaError.code) {
				case MediaError.MEDIA_ERR_ABORTED:
					errorMessage = 'Video loading was aborted';
					break;
				case MediaError.MEDIA_ERR_NETWORK:
					errorMessage = 'Network error while loading video';
					errorCategory = ErrorCategory.NETWORK;
					break;
				case MediaError.MEDIA_ERR_DECODE:
					errorMessage = 'Video decoding error';
					break;
				case MediaError.MEDIA_ERR_SRC_NOT_SUPPORTED:
					errorMessage = 'Video format not supported';
					errorCategory = ErrorCategory.STREAM;
					break;
				default:
					errorMessage = `Video error (code: ${mediaError.code})`;
			}
		}

		const streamingError = new StreamingError({
			category: errorCategory,
			severity: ErrorSeverity.HIGH,
			message: errorMessage,
			originalError: new Error(errorMessage),
			context: {
				metadata: {
					mediaErrorCode: mediaError?.code,
					mediaErrorMessage: mediaError?.message,
					videoSrc: proxiedSrc,
					networkState: videoElement.networkState,
					readyState: videoElement.readyState
				}
			},
			recoverable: true,
			retryable: errorCategory === ErrorCategory.NETWORK || errorCategory === ErrorCategory.STREAM
		});

		handleStreamingError(streamingError);
	}

	function handleAbort(): void {
		logger.player('info', 'Video loading aborted');
		updatePlayerState();
	}

	function handleStalled(): void {
		logger.player('warn', 'Video stalled');
		isBuffering = true;
		updatePlayerState();
	}

	function handleSuspend(): void {
		logger.player('debug', 'Video loading suspended');
		updatePlayerState();
	}
</script>

<div class="relative w-full bg-black">
	{#if isBuffering && loadProgress < 100}
		<div class="absolute inset-0 z-10 flex items-center justify-center bg-black/80">
			<div class="text-center">
				<div
					class="mb-4 inline-block h-12 w-12 animate-spin rounded-full border-b-2 border-red-600"
				></div>
				<div class="mb-2 text-lg text-white">
					{loadProgress < 25
						? 'Connecting...'
						: loadProgress < 50
							? 'Loading metadata...'
							: loadProgress < 75
								? 'Buffering...'
								: 'Almost ready...'}
				</div>
				<div class="mb-2 h-2 w-64 rounded-full bg-gray-700">
					<div
						class="h-2 rounded-full bg-red-600 transition-all duration-300"
						style="width: {loadProgress}%"
					></div>
				</div>
				<div class="text-sm text-gray-400">{Math.round(loadProgress)}% loaded</div>
			</div>
		</div>
	{/if}

	<video
		bind:this={videoElement}
		class="h-auto w-full"
		controls
		{poster}
		preload="auto"
		crossorigin="anonymous"
	>
		<track kind="captions" />
		<p class="p-4 text-white">
			Your browser doesn't support HTML5 video.
			<a href={proxiedSrc} class="text-red-500 hover:text-red-400"> Download the video file </a>.
		</p>
	</video>

	<!-- Buffer indicator -->
	{#if !isBuffering && bufferHealth < 50}
		<div class="absolute right-4 bottom-16 left-4 z-10">
			<div class="rounded bg-black/70 px-3 py-2 text-sm text-white">
				<div class="flex items-center space-x-2">
					<div class="flex-shrink-0">
						<div class="h-2 w-2 animate-pulse rounded-full bg-yellow-500"></div>
					</div>
					<span>Buffer: {Math.round(bufferHealth)}%</span>
					<div class="h-1 flex-1 rounded-full bg-gray-600">
						<div
							class="h-1 rounded-full bg-yellow-500 transition-all duration-300"
							style="width: {bufferHealth}%"
						></div>
					</div>
				</div>
			</div>
		</div>
	{/if}
</div>

<style>
	video::-webkit-media-controls {
		background-color: rgba(0, 0, 0, 0.8);
	}

	video::-webkit-media-controls-panel {
		background-color: rgba(0, 0, 0, 0.8);
	}
</style>
