<script lang="ts">
	import { Play, Monitor, Wifi, HardDrive, Star, Calendar, Clock } from 'lucide-svelte';
	import type { StremioStream, StremioMeta } from '$lib/services/stremio';

	interface Props {
		streams: StremioStream[];
		meta: StremioMeta;
		onStreamSelect: (stream: StremioStream) => void;
		onCancel: () => void;
	}

	let { streams, meta, onStreamSelect, onCancel }: Props = $props();

	// Enhanced stream parsing to extract provider and quality information
	interface ParsedStream {
		stream: StremioStream;
		provider: string;
		quality: string;
		size: string;
		seeders?: string;
		type: 'torrent' | 'direct' | 'debrid';
		confidence: number;
	}

	const parseStreams = (streams: StremioStream[]): ParsedStream[] => {
		return streams.map((stream) => {
			const title = stream.title || stream.name || '';
			const description = stream.description || '';
			
			// Extract provider information
			let provider = 'Unknown';
			let type: 'torrent' | 'direct' | 'debrid' = 'direct';
			
			if (title.toLowerCase().includes('real-debrid') || title.toLowerCase().includes('rd')) {
				provider = 'Real-Debrid';
				type = 'debrid';
			} else if (title.toLowerCase().includes('torrentio')) {
				provider = 'Torrentio';
				type = 'torrent';
			} else if (title.toLowerCase().includes('torbox')) {
				provider = 'TorBox';
				type = 'debrid';
			} else if (title.toLowerCase().includes('premiumize')) {
				provider = 'Premiumize';
				type = 'debrid';
			} else if (title.toLowerCase().includes('alldebrid')) {
				provider = 'AllDebrid';
				type = 'debrid';
			} else {
				// Try to extract from URL or other indicators
				const streamUrl = stream.url.toLowerCase();
				if (streamUrl.includes('real-debrid.com')) {
					provider = 'Real-Debrid';
					type = 'debrid';
				} else if (streamUrl.includes('torrentio')) {
					provider = 'Torrentio';
					type = 'torrent';
				}
			}

			// Extract quality information
			let quality = 'Unknown';
			let confidence = 0;
			
			const qualityPatterns = [
				{ pattern: /2160p|4k/i, quality: '4K (2160p)', confidence: 5 },
				{ pattern: /1440p/i, quality: '1440p', confidence: 4 },
				{ pattern: /1080p/i, quality: '1080p', confidence: 4 },
				{ pattern: /720p/i, quality: '720p', confidence: 3 },
				{ pattern: /480p/i, quality: '480p', confidence: 2 },
				{ pattern: /360p/i, quality: '360p', confidence: 1 }
			];

			for (const { pattern, quality: q, confidence: c } of qualityPatterns) {
				if (pattern.test(title) || pattern.test(description)) {
					quality = q;
					confidence = c;
					break;
				}
			}

			// Extract file size
			let size = '';
			const sizeMatch = title.match(/(\d+(?:\.\d+)?)\s?(GB|MB|TB)/i);
			if (sizeMatch) {
				size = `${sizeMatch[1]} ${sizeMatch[2].toUpperCase()}`;
			}

			// Extract seeders for torrents
			let seeders = undefined;
			const seedersMatch = title.match(/üë§\s*(\d+)/);
			if (seedersMatch) {
				seeders = seedersMatch[1];
			}

			return {
				stream,
				provider,
				quality,
				size,
				seeders,
				type,
				confidence
			};
		}).sort((a, b) => {
			// Sort by confidence (quality), then by type preference (debrid > direct > torrent)
			if (a.confidence !== b.confidence) {
				return b.confidence - a.confidence;
			}
			const typeOrder = { debrid: 3, direct: 2, torrent: 1 };
			return typeOrder[b.type] - typeOrder[a.type];
		});
	};

	const parsedStreams = $derived(parseStreams(streams));
	const groupedStreams = $derived(() => {
		const groups: Record<string, ParsedStream[]> = {};
		parsedStreams.forEach(stream => {
			if (!groups[stream.provider]) {
				groups[stream.provider] = [];
			}
			groups[stream.provider].push(stream);
		});
		return groups;
	});

	const getProviderIcon = (type: 'torrent' | 'direct' | 'debrid') => {
		switch (type) {
			case 'debrid': return HardDrive;
			case 'direct': return Wifi;
			case 'torrent': return Monitor;
			default: return Monitor;
		}
	};

	const getQualityColor = (quality: string) => {
		if (quality.includes('4K')) return 'text-purple-400';
		if (quality.includes('1080p')) return 'text-blue-400';
		if (quality.includes('720p')) return 'text-green-400';
		if (quality.includes('480p')) return 'text-yellow-400';
		return 'text-gray-400';
	};

	const getTypeColor = (type: 'torrent' | 'direct' | 'debrid') => {
		switch (type) {
			case 'debrid': return 'bg-green-600';
			case 'direct': return 'bg-blue-600';
			case 'torrent': return 'bg-orange-600';
			default: return 'bg-gray-600';
		}
	};
</script>

<div class="min-h-screen bg-black">
	<!-- Header -->
	<div class="border-b border-gray-800 bg-gray-900/50 px-4 py-6 sm:px-6">
		<div class="mx-auto max-w-4xl">
			<div class="flex items-start gap-6">
				{#if meta.poster}
					<img
						src={meta.poster}
						alt={meta.name}
						class="h-32 w-24 flex-shrink-0 rounded-lg object-cover sm:h-40 sm:w-28"
					/>
				{/if}
				<div class="flex-1">
					<h1 class="mb-2 text-xl font-bold text-white sm:text-2xl md:text-3xl">{meta.name}</h1>
					<div class="mb-3 flex flex-wrap items-center gap-3 text-sm text-gray-400">
						{#if meta.year}
							<div class="flex items-center gap-1">
								<Calendar class="h-4 w-4" />
								<span>{meta.year}</span>
							</div>
						{/if}
						{#if meta.runtime}
							<div class="flex items-center gap-1">
								<Clock class="h-4 w-4" />
								<span>{meta.runtime}</span>
							</div>
						{/if}
						{#if meta.imdbRating}
							<div class="flex items-center gap-1">
								<Star class="h-4 w-4 text-yellow-400" />
								<span>{meta.imdbRating}/10</span>
							</div>
						{/if}
					</div>
					{#if meta.description}
						<p class="text-sm leading-relaxed text-gray-300 sm:text-base">
							{meta.description.length > 200 ? meta.description.slice(0, 200) + '...' : meta.description}
						</p>
					{/if}
				</div>
			</div>
		</div>
	</div>

	<!-- Stream Selection -->
	<div class="mx-auto max-w-4xl px-4 py-8 sm:px-6">
		<div class="mb-6">
			<h2 class="mb-2 text-xl font-bold text-white">Choose Stream Quality</h2>
			<p class="text-sm text-gray-400">
				Select your preferred streaming source and quality. Higher quality streams may take longer to load.
			</p>
		</div>

		{#if parsedStreams.length === 0}
			<div class="rounded-lg border border-gray-700 bg-gray-800/50 p-8 text-center">
				<div class="mb-4 text-4xl opacity-50">üö´</div>
				<h3 class="mb-2 text-lg font-semibold text-white">No Streams Available</h3>
				<p class="text-sm text-gray-400">No streaming sources found for this content.</p>
			</div>
		{:else}
			<!-- Provider Groups -->
			<div class="space-y-6">
				{#each Object.entries(groupedStreams) as [providerName, providerStreams]}
					{@const Icon = getProviderIcon(providerStreams[0].type)}
					<div class="rounded-lg border border-gray-700 bg-gray-800/30 p-4">
						<h3 class="mb-4 flex items-center gap-2 text-lg font-semibold text-white">
							<Icon class="h-5 w-5" />
							{providerName}
							<span class="{getTypeColor(providerStreams[0].type)} rounded-full px-2 py-1 text-xs font-medium text-white">
								{providerStreams[0].type.toUpperCase()}
							</span>
						</h3>

						<div class="grid gap-3 sm:grid-cols-2 lg:grid-cols-3">
							{#each providerStreams as parsed}
								<button
									class="group rounded-lg border border-gray-600 bg-gray-700/50 p-4 text-left transition-all hover:border-red-500 hover:bg-gray-700 focus:border-red-500 focus:outline-none focus:ring-2 focus:ring-red-500"
									onclick={() => onStreamSelect(parsed.stream)}
								>
									<div class="mb-2 flex items-center justify-between">
										<span class="{getQualityColor(parsed.quality)} text-sm font-semibold">
											{parsed.quality}
										</span>
										<Play class="h-4 w-4 text-gray-400 opacity-0 transition-opacity group-hover:opacity-100" />
									</div>

									{#if parsed.size}
										<div class="mb-2 text-xs text-gray-400">
											Size: {parsed.size}
										</div>
									{/if}

									{#if parsed.seeders}
										<div class="mb-2 flex items-center gap-1 text-xs text-gray-400">
											<span>üë§</span>
											<span>{parsed.seeders} seeders</span>
										</div>
									{/if}

									<div class="text-xs text-gray-500">
										{parsed.stream.title || parsed.stream.name || 'Stream'}
									</div>
								</button>
							{/each}
						</div>
					</div>
				{/each}
			</div>
		{/if}

		<!-- Actions -->
		<div class="mt-8 flex justify-center gap-4">
			<button
				class="rounded-lg bg-gray-600 px-6 py-3 font-medium text-white transition-colors hover:bg-gray-500 focus:outline-none focus:ring-2 focus:ring-gray-500"
				onclick={onCancel}
			>
				Cancel
			</button>
		</div>

		<!-- Stream Count Info -->
		<div class="mt-6 text-center">
			<p class="text-xs text-gray-500">
				Found {streams.length} stream{streams.length === 1 ? '' : 's'} from {Object.keys(groupedStreams).length} provider{Object.keys(groupedStreams).length === 1 ? '' : 's'}
			</p>
		</div>
	</div>
</div>